
import { defineComponent, ref, computed, nextTick } from "vue";

import * as Connection from "../../src/connection";

import MidiFilterChain from "./midi-in-filter-chain-component.vue";

import Page from "../a/page-component.vue";
import Card from "../a/card-component.vue";
import CardBody from "../a/card-body.vue";
import Row from "../a/row-component.vue";
import RowCell from "../a/row-cell.vue";
import BtnHref from "../a/btn-href.vue";
import RowCellRight from "../a/row-cell-right.vue";
import Btn from "../a/btn-component.vue";

export default defineComponent({
  components: {
    MidiFilterChain,
    Page,
    Card,
    CardBody,
    Row,
    RowCell,
    RowCellRight,
    Btn,
    BtnHref,
  },
  props: {
    midiId: {
      type: String,
      required: true,
    },
  },
  setup(props) {
    const midiRouteInput = ref(
      Connection.loginStatus.userDataConfig.getMidiRouteInput(
          Connection.loginStatus.inPorts[Number(props.midiId)]
    ));

    function _getInPortsWithoutSelfClockPropegate() {
      const keysSelected =
        midiRouteInput.value.midiRouteClock.propegateInputs.map((row) => {
          return row.midiInputId;
        });
      return midiRouteInput.value.inPortsWithoutSelf.filter((row) => {
        return keysSelected.indexOf(row.midiInputId) === -1;
      });
    }

    const inPortsWithoutSelfClockPropegate = ref(
      _getInPortsWithoutSelfClockPropegate()
    );

    const propegateInputToAdd = ref("");

    const cc14bitChannel = ref(1);
    const cc14bitCc = ref(0);

    function forceUpdate() {
      const n = cc14bitCc.value;
      cc14bitCc.value = -1;
      nextTick(() => {
        cc14bitCc.value = n;
      });
    }

    function spliceRouterChain(idx: number) {
      midiRouteInput.value.midiRouterChains.splice(idx, 1);
      forceUpdate();
    }

    function spliceCc14bit(idx: number) {
      midiRouteInput.value.cc14bitAry.splice(idx, 1);
      forceUpdate();
    }

    function addCc14bit() {
      if (
        cc14bitChannel.value < 1 ||
        cc14bitChannel.value > 16 ||
        cc14bitCc.value < 0 ||
        cc14bitCc.value > 17
      ) {
        return;
      }
      if (
        midiRouteInput.value.cc14bitAry.filter((row) => {
          return (
            row.channel === cc14bitChannel.value && row.cc === cc14bitCc.value
          );
        }).length > 0
      ) {
        return;
      }
      midiRouteInput.value.addCc14bitAry(cc14bitChannel.value, cc14bitCc.value);

      forceUpdate();
    }

    function _resetInPortsWithoutSelfClockPropegate() {
      inPortsWithoutSelfClockPropegate.value.splice(
        0,
        inPortsWithoutSelfClockPropegate.value.length
      );
      _getInPortsWithoutSelfClockPropegate().forEach((row) => {
        inPortsWithoutSelfClockPropegate.value.push(row);
      });
      propegateInputToAdd.value = "";
    }

    function spliceClockPropegateInput(inputsId: number) {
      for (
        let i = 0;
        i < midiRouteInput.value.midiRouteClock.propegateInputs.length;
        i++
      ) {
        if (
          midiRouteInput.value.midiRouteClock.propegateInputs[i].midiInputId ===
          inputsId
        ) {
          midiRouteInput.value.midiRouteClock.propegateInputs.splice(i, 1);
          continue;
        }
      }

      _resetInPortsWithoutSelfClockPropegate();
    }

    function addClockPropegateInput() {
      if (propegateInputToAdd.value === "") {
        return;
      }

      midiRouteInput.value.addClockPropegateInput(
        parseInt(propegateInputToAdd.value)
      );
      _resetInPortsWithoutSelfClockPropegate();
    }

    function addChain() {
      midiRouteInput.value.addMidiRouterChain("New chain");
      forceUpdate();
    }

    const headrMsg = computed(() => {
      return `${props.midiId} - ${
        Connection.loginStatus.inPorts[parseInt(props.midiId)]
      }`;
    });

    const setChainItemName=(s: string, item: typeof midiRouteInput.value.midiRouterChains[0] )=>{
      item.name = s;
    }

    return {
      headrMsg,
      addChain,
      addClockPropegateInput,
      spliceClockPropegateInput,
      addCc14bit,
      spliceCc14bit,
      spliceRouterChain,
      cc14bitCc,
      cc14bitChannel,
      propegateInputToAdd,
      inPortsWithoutSelfClockPropegate,
      midiRouteInput,
      setChainItemName,
    };
  },
});
